<template>


	<scroll-view scroll-y class="scrollview" :style="'height:'+height+'px'">
		<view class="" style="margin: 20upx;flex-flow: row;justify-content: flex-end;padding-right: 10upx;">
			<view v-if="!modify_flag" class="icontext" style="padding-right:40upx ;" @click="tap_new">&#xe641;</view>
			<view v-if="!modify_flag" class="icontext" @click="tap_modify">&#xe603;</view>
		</view>


		<form @submit="formSubmit" @reset="formReset">
			<view class="white_bac">
				<view v-for="(item1,index1) in luyouData_ziduan" :key="index1" v-if="show_list">


					<view class="linestyle" v-if="item1.ziduan_type =='txt'&&item1.card_id ==card_id">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right" v-if="!modify_flag"> {{data_json[item1.ziduan_name] +item1.ziduan_buchong}} </view>
						<view class="linestyle_right" v-if="modify_flag">
							<input class="linestyle_right_left" :id="index1" :name="item1.ziduan_name" :value="data_json[item1.ziduan_name]"
							 :placeholder="item1.important_flag=='important'?'不可空':'可空'" @input="input_change" />
							<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
							<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>
						</view>
					</view>

					<view class="linestyle" v-if="item1.ziduan_type =='only_read'&&item1.card_id ==card_id&&!modify_flag">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right"> {{data_json[item1.ziduan_name] +item1.ziduan_buchong}} </view>
					</view>

					<view class="linestyle" v-if="item1.ziduan_type =='picture'&&item1.card_id ==card_id &&!modify_flag">
						<view class="img_view" :style="'background-image:url('+data_json[item1.ziduan_name]+');'"></view>
						<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
						<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>
					</view>

					<view class="linestyle" v-if="item1.ziduan_type =='url'&&item1.card_id ==card_id&&!modify_flag">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right" :href="data_json.app_detail_url">{{data_json[item1.ziduan_name]}}
						</view>
					</view>

					<view class="linestyle" v-if="item1.ziduan_type =='picker1'&&item1.card_id ==card_id">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right" v-if="!modify_flag"> {{data_json[item1.ziduan_name] +item1.ziduan_buchong}} </view>
						<view v-if="modify_flag" class="linestyle_right">
							<view class="linestyle_right_left">
								<picker class=" border-style-blue icontext blue " :name="item1.ziduan_name" :id="index1" :range="item1.fanweishuzu"
								 @change="pickerChange1">{{item1.fanweishuzu[picker_select1]}}&nbsp;&nbsp;&#xe602;
								 </picker>
							</view>
							<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
							<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>
						</view>
					</view>

					<view class="linestyle" v-if="item1.ziduan_type =='picker2'&&item1.card_id ==card_id">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right" v-if="!modify_flag"> {{data_json[item1.ziduan_name] +item1.ziduan_buchong}} </view>
						<view v-if="modify_flag" class="linestyle_right">
							<view class="linestyle_right_left">
								<picker class=" border-style-blue icontext blue" :name="item1.ziduan_name" :id="index1" :range="item1.fanweishuzu"
								 @change="pickerChange2">{{item1.fanweishuzu[picker_select2]}}&nbsp;&nbsp;&#xe602;
								 </picker>
							</view>
							<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
							<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>
						</view>
					</view>

					<view class="linestyle" v-if="item1.ziduan_type =='picker3'&&item1.card_id ==card_id">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right" v-if="!modify_flag"> {{data_json[item1.ziduan_name] +item1.ziduan_buchong}} </view>
						<view v-if="modify_flag" class="linestyle_right">
							<view class="linestyle_right_left">
								<picker class=" border-style-blue icontext blue" :name="item1.ziduan_name" :id="index1" :range="item1.fanweishuzu"
								 @change="pickerChange3">{{item1.fanweishuzu[picker_select3]}}&nbsp;&nbsp;&#xe602;
								 </picker>
							</view>
							<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
							<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>
						</view>
					</view>


					<view class="linestyle" v-if="item1.ziduan_type =='picker_day'&&item1.card_id ==card_id">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right" v-if="!modify_flag"> {{data_json[item1.ziduan_name] +item1.ziduan_buchong}} </view>
						<view v-if="modify_flag" class="linestyle_right">
							<view class="linestyle_right_left">
								<picker class=" border-style-blue icontext blue" :name="item1.ziduan_name" :id="index1" mode="date" start="1949-01-01"
								 end="2022-01-01" @change="dateChange_day">
									{{picker_date_day}}&nbsp;&nbsp;&#xe602;
								</picker>
							</view>
							<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
							<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>

						</view>

					</view>


					<view class="linestyle" v-if="item1.ziduan_type =='picker_month'&&item1.card_id ==card_id">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right" v-if="!modify_flag"> {{data_json[item1.ziduan_name] +item1.ziduan_buchong}} </view>
						<view v-if="modify_flag" class="linestyle_right">
							<view class="linestyle_right_left">
								<picker class=" border-style-blue icontext blue" :name="item1.ziduan_name" :id="index1" mode="date" start="1949-01-01"
								 end="2022-01-01" @change="dateChange_month" fields="month">
									{{picker_date_month}}&nbsp;&nbsp;&#xe602;
								</picker>
							</view>
							<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
							<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>
						</view>

					</view>




					<view class="linestyle" v-if="item1.ziduan_type =='picker_year'&&item1.card_id ==card_id">
						<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
						<view class="linestyle_right" v-if="!modify_flag"> {{data_json[item1.ziduan_name] +item1.ziduan_buchong}} </view>
						<view v-if="modify_flag" class="linestyle_right">
							<view class="linestyle_right_left">
								<picker class=" border-style-blue icontext blue" :name="item1.ziduan_name" :id="index1" mode="date" start="1949-01-01"
								 end="2022-01-01" @change="dateChange_year" fields="year">
									{{picker_date_year}}&nbsp;&nbsp;&#xe602;
								</picker>

							</view>

							<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
							<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>

						</view>

					</view>




					<view v-if="item1.ziduan_type =='bigtext'&&item1.card_id ==card_id" style="display: flex;flex-flow: column;">
						<view class="linestyle">


							<view class="linestyle_left"> {{item1.ziduan_zhongwen}}:</view>
							<view class="linestyle_right" v-if="!modify_flag"> {{data_json[item1.ziduan_name] }} </view>
							<view class="linestyle_right" v-if="modify_flag">
								<view class="linestyle_right_right"></view>
								<view v-if="(item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon red">&#xe66f;</view>
								<view v-if="(!item1.linshi_value)&&modify_flag&&(!new_item_flag)" class="icontext text_view_icon green">&#xe625;</view>
							</view>

						</view>
						<textarea class="text_view_bigtext" v-if="modify_flag" maxlength="1000" @input="bigtext_input" :id="index1"
						 auto-height :name="item1.ziduan_name" :value="data_json[item1.ziduan_name]" :placeholder="item1.important_flag=='important'?'不可空':'可空'" />
						</textarea>
					</view>






				</view>



			</view>
			<view class="" v-if="modify_flag&&!new_item_flag">
				<button formType="submit" class="tijiao_button_style">
					提交
				</button>
			</view>
			<view class="" v-if="modify_flag&&new_item_flag">
				<button formType="submit" class="xinjian_button_style">
					新建
				</button>
			</view>

		</form>



	</scroll-view>
</template>
<script>
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				height: 0,
				new_item_flag: false,
				data_json_old: null,
				data_json: null,
				card_id: null,
				modify_flag: false,
				show_list: false,
				luyouData_ziduan: [],
				picker_select1: 0,
				picker_select2: 0,
				picker_select3: 0,
				picker_date_day: '2018-01-01',
				picker_date_month: '2018-01',
				picker_date_year: '2018',
			}
		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])
		},
		onLoad: async function(option1) {
			this.height = uni.getSystemInfoSync().windowHeight;
			this.card_id = option1.card_id;
			this.new_item_flag = option1.new_item_flag;
			if (this.new_item_flag) {
				this.modify_flag = true;
			}

			
			console.log("item_detail：card_id:" + option1.card_id)
			this.data_json = JSON.parse(option1.data)
			console.log(JSON.parse(option1.data))
			this.data_json_old = JSON.parse(option1.data)
			let aa = await this.get_hot_card_list_ziduan()
		    console.log(this.luyouData_ziduan)
			this.find_ziduan_fanwei();
			// console.log(this.luyouData_ziduan)

		},


		methods: {

			tap_modify: function() {
				this.modify_flag = true;
			},

			tap_new: function() {
				
				var data={'ziduan_name':'','ziduan_zhongwen':'',}
				uni.navigateTo({
					url: "../item_detail/item_detail?data=" + JSON.stringify(data) + "&card_id=" + this.card_id + "&new_item_flag=true",
				})
			},
			input_change(evt) {
				if (this.data_json[this.luyouData_ziduan[evt.currentTarget.id].ziduan_name] != evt.detail.value) {
					this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = evt.detail.value;
					this.data_json[this.luyouData_ziduan[evt.currentTarget.id]['ziduan_name']] = evt.detail.value;
				}
				// this.inputValue = e.detail.value;
			},
			bigtext_input(evt) {
				if (this.data_json[this.luyouData_ziduan[evt.currentTarget.id].ziduan_name] != evt.detail.value) {
					this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = evt.detail.value;
					this.data_json[this.luyouData_ziduan[evt.currentTarget.id]['ziduan_name']] = evt.detail.value;
				}
				// this.inputValue = e.detail.value;
			},
			pickerChange1(evt) {
				if (this.data_json[this.luyouData_ziduan[evt.currentTarget.id].ziduan_name] != evt.detail.value) {
					// this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = evt.detail.value;
					this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = this.luyouData_ziduan[evt.currentTarget.id][
						'fanweishuzu'
					][evt.detail.value];
					this.data_json[this.luyouData_ziduan[evt.currentTarget.id]['ziduan_name']] = evt.detail.value;
				}
				this.picker_select1 = evt.detail.value;
			},
			pickerChange2(evt) {
				if (this.data_json[this.luyouData_ziduan[evt.currentTarget.id].ziduan_name] != evt.detail.value) {
					this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = this.luyouData_ziduan[evt.currentTarget.id][
						'fanweishuzu'
					][evt.detail.value];
					this.data_json[this.luyouData_ziduan[evt.currentTarget.id]['ziduan_name']] = evt.detail.value;
				}
				this.picker_select2 = evt.detail.value;
			},
			pickerChange3(evt) {
				if (this.data_json[this.luyouData_ziduan[evt.currentTarget.id].ziduan_name] != evt.detail.value) {
					this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = this.luyouData_ziduan[evt.currentTarget.id][
						'fanweishuzu'
					][evt.detail.value];
					this.data_json[this.luyouData_ziduan[evt.currentTarget.id]['ziduan_name']] = evt.detail.value;
				}
				this.picker_select3 = evt.detail.value;
			},
			dateChange_day(evt) {
				if (this.data_json[this.luyouData_ziduan[evt.currentTarget.id].ziduan_name] != evt.detail.value) {
					this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = evt.detail.value;
					this.data_json[this.luyouData_ziduan[evt.currentTarget.id]['ziduan_name']] = evt.detail.value;
				}
				this.picker_date_day = evt.detail.value;
			},
			dateChange_month(evt) {
				if (this.data_json[this.luyouData_ziduan[evt.currentTarget.id].ziduan_name] != evt.detail.value) {
					this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = evt.detail.value;
					this.data_json[this.luyouData_ziduan[evt.currentTarget.id]['ziduan_name']] = evt.detail.value;
				}
				this.picker_date_month = evt.detail.value;
			},
			dateChange_year(evt) {
				if (this.data_json[this.luyouData_ziduan[evt.currentTarget.id].ziduan_name] != evt.detail.value) {
					this.luyouData_ziduan[evt.currentTarget.id]['linshi_value'] = evt.detail.value;
					this.data_json[this.luyouData_ziduan[evt.currentTarget.id]['ziduan_name']] = evt.detail.value;
				}
				this.picker_date_year = evt.detail.value;
			},



			formSubmit: async function(e) {
				var that = this;
				var randnum = Math.floor(Math.random() * 90000) + 100000;
				var randnstr = (new Date().valueOf()) + String(randnum);
				var important_name = '';
				var important_value = null;
				
				for (let i = 0; i < that.luyouData_ziduan.length; i++) {
					if (that.luyouData_ziduan[i].important_flag == 'important' && that.luyouData_ziduan[i].card_id == that.card_id &&
						!that.new_item_flag) {
						important_name = that.data_json[that.luyouData_ziduan[i].ziduan_name]
					}
					if (that.luyouData_ziduan[i].important_flag == 'important' && that.luyouData_ziduan[i].card_id == that.card_id &&
						that.new_item_flag) {
						important_name = that.luyouData_ziduan[i].ziduan_name
						important_value = that.luyouData_ziduan[i].linshi_value;
					}
				}

				if (that.new_item_flag && important_value == null) {
					uni.showModal({
						title: "必填项，不可为空"
					})
					
					return 0;
				}
				else{
					uni.showLoading({
						title: '上传中'
					})
				}

				for (let i = 0; i < that.luyouData_ziduan.length; i++) {
					if (that.luyouData_ziduan[i].linshi_value) {
						// that.upload_modify(that.luyouData_ziduan[i], randnstr, important_name)
						await this.timeout(1000);
						that.upload_to_server({
							'form': 'modify_or_build_item',
							'nickName': that.userInfo.nickName,
							'phonenum': that.phonenum,
							'avatarUrl': that.userInfo.avatarUrl,
							'openid': that.openid,
							'card_id': that.luyouData_ziduan[i].card_id,
							'ziduan_id': that.luyouData_ziduan[i].ziduan_id,
							'ziduan_zhongwen': that.luyouData_ziduan[i].ziduan_zhongwen,
							'ziduan_name': that.luyouData_ziduan[i].ziduan_name,
							'linshi_value': that.luyouData_ziduan[i].linshi_value,
							'item_id': that.data_json_old.item_id,
							'old_value': that.data_json_old[that.luyouData_ziduan[i].ziduan_name],
							'modify_or_new_flag': (that.new_item_flag ? 'build' : 'modify'),
							'important_name': important_name,
							'shuijishu': randnstr,
						})
					}
				}
				// console.log('end')
				uni.hideLoading()

				if (that.new_item_flag) {
					uni.navigateBack();
				} else {
					that.modify_flag = false;
				}
				// console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			},


			find_ziduan_fanwei: function() {
				var that = this;
				for (let i = 0; i < that.luyouData_ziduan.length; i++) {
					if (that.luyouData_ziduan[i]['ziduan_type'] == 'picker_year' && that.luyouData_ziduan[i]['card_id'] == that.card_id) {
						that.picker_date_year = that.data_json[that.luyouData_ziduan[i].ziduan_name]
					}
					if (that.luyouData_ziduan[i]['ziduan_type'] == 'picker_month' && that.luyouData_ziduan[i]['card_id'] == that.card_id) {
						that.picker_date_month = that.data_json[that.luyouData_ziduan[i].ziduan_name]
					}
					if (that.luyouData_ziduan[i]['ziduan_type'] == 'picker_day' && that.luyouData_ziduan[i]['card_id'] == that.card_id) {
						that.picker_date_day = that.data_json[that.luyouData_ziduan[i].ziduan_name]
					}
					that.luyouData_ziduan[i]['fanweishuzu'] = [];
					if (that.luyouData_ziduan[i].ziduan_fanwei) {
						var fanweishuzu = that.luyouData_ziduan[i].ziduan_fanwei.split(',');
						// console.log(fanweishuzu)
						that.luyouData_ziduan[i]['fanweishuzu'] = fanweishuzu;
						
						for (let j = 0; j < fanweishuzu.length; j++) {
							if (that.data_json[that.luyouData_ziduan[i].ziduan_name] == fanweishuzu[j]) {
								if (that.luyouData_ziduan[i]['ziduan_type'] == 'picker1') {
									that.picker_select1 = j;
								}
								if (that.luyouData_ziduan[i]['ziduan_type'] == 'picker2') {
									that.picker_select2 = j;
								}
								if (that.luyouData_ziduan[i]['ziduan_type'] == 'picker3') {
									that.picker_select3 = j;
								}
							}
						}
					}
				}
				// console.log(that.luyouData_ziduan)
				this.show_list=true;
			},
			timeout: function(ms) {
				return new Promise(resolve => setTimeout(resolve, ms));
			}


		}
	}
</script>

<style>
	/* 	
	::-webkit-scrollbar {
		width: 0;
		height: 0;
		color: transparent;
	} */
	.border-style-blue {
		border: 2px solid blue;
		font-weight: normal;
		width: 180upx;
		font-size: 25upx;
		padding: 0 12upx;
	}

	.img_view {
		width: 100upx;
		height: 100upx;
		margin: 10upx;
		border-radius: 20upx;
		background-size: 100%100%;

	}

	.scrollview {
		display: flex;
		flex-flow: column nowrap;
		width: 750upx;
	}
</style>



